<template>
  <div class="content-networkTest">
    <headerUsually btnShow="true" titleText="网络测试" url="/mobileOrdering/login"></headerUsually>
    <div class="content-networkTeat-main">
      <div class="content-ip">
        <div class="label">IP地址：</div>
        <input class="input" v-model="ip" placeholder="请输入IP地址" />
      </div>
      <div class="content-ip">
        <div class="label">端口号：</div>
        <input class="input" v-model="port" placeholder="请输入端口号" />
      </div>
      <div class="btn cancel-btn" @click="networkTest()">测速</div>
      <div class="btn confirm-btn" @click="okFun()">确定</div>
    </div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
export default {
  components: {
    headerUsually
  },

  data() {
    return {
      ip: "",
      port: ""
    };
  },
  mounted() {
    let dcgateway = localStorage.getItem("dcgateway")
      ? JSON.parse(localStorage.getItem("dcgateway"))
      : {};
    this.ip = dcgateway.ip;
    this.port = dcgateway.port;
  },
  methods: {
    okFun() {
      var ipEXP = /(2(5[0-5]{1}|[0-4]\d{1})|[0-1]?\d{1,2})(\.(2(5[0-5]{1}|[0-4]\d{1})|[0-1]?\d{1,2})){3}/g;
      if (!ipEXP.test(this.ip.trim())) {
        this.msg("IP地址不正确");
        return;
      }
      let portEXP = /^([0-9]|[1-9]\d|[1-9]\d{2}|[1-9]\d{3}|[1-5]\d{4}|6[0-4]\d{3}|65[0-4]\d{2}|655[0-2]\d|6553[0-5])$/;
      if (!portEXP.test(this.port.trim())) {
        this.msg("端口号不正确");
        return;
      }
      let dcgateway = localStorage.getItem("dcgateway")
        ? JSON.parse(localStorage.getItem("dcgateway"))
        : {};
      dcgateway.ip = this.ip.trim();
      dcgateway.port = this.port.trim();
      localStorage.setItem("dcgateway", JSON.stringify(dcgateway));
      this.$router.push("/mobileOrdering/login");
    },
    networkTest() {
      let ipEXP = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/;
      if (!ipEXP.test(this.ip.trim())) {
        this.msg("IP地址不正确");
        return;
      }
      let portEXP = /^([0-9]|[1-9]\d|[1-9]\d{2}|[1-9]\d{3}|[1-5]\d{4}|6[0-4]\d{3}|65[0-4]\d{2}|655[0-2]\d|6553[0-5])$/;
      if (!portEXP.test(this.port.trim())) {
        this.msg("端口号不正确");
        return;
      }
      this.$http
        .get(
          `http://${this.ip.trim()}:${this.port.trim()}/pos/posUnion/ping?testCase`,
          { vv: 6 }
        )
        .then(res => {
          console.log("networkTest:", res);
          if (res.status == 200) {
            this.msg("网络畅通！");
          } else {
            this.msg(res.data.errorMsg);
          }
        });
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>

<style lang="less" scoped>
.content-networkTeat-main {
  padding-top: 46px;
  .content-ip {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 17px 15px;
    background-color: #fff;
    margin-bottom: 12px;
    .label {
      font-size: 14px;
      color: #666666;
    }
    .input {
      font-size: 14px;
      color: #000000;
      text-align: center;
      flex: 1;
      outline: none;
      border: none;
    }
  }
  .btn {
    margin-top: 20px;
    background: #ffffff;
    font-size: 18px;
    color: #c4ad68;
    text-align: center;
    padding: 11px;
  }
}
</style>



